import React from "react";

function PrescriptionCards({ onClick, cards }) {
  return (
    <div className="patient__prescription-forms px-3 py-3">
      {cards?.map((card, index) => (
        <div key={index} className="patient__prescription-cards" onClick={() => onClick(card.formId, card.title, card.patientFormAttempt)}>
          {card.title}
        </div>
      ))}
    </div>
  );
}

export default PrescriptionCards;
