import React, { useEffect, useState } from "react";
import { Button, Card, Col, Form, Modal, Row } from "react-bootstrap";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { IoIosArrowForward } from "react-icons/io";
import { useDispatch, useSelector } from "react-redux";
import {
  getAdminPresForm,
  getAllAdminPresForm,
} from "../../../../app/features/OnlinePrescriptionForm/OnlinePrescriptionForm.slice";
import FullScreenLoader from "../../../../authorization/FullScreenLoader";
import { useForm } from "react-hook-form";
import SubscriptionPlanModal from "../SubscriptionPlanModal";
import { PATH } from "../../../../utils/path";
import { createPatientPresForm } from "../../../../app/features/PatientOnlinePrescription/PatientOnlinePrescription.slice";
import { ClipLoader } from "react-spinners";

function DiseaseForm({ diseaseId }) {
  const [formData, setFormData] = useState({});
  const [showSubscription, setShowSubscription] = useState(false);
  const [showConfirmation, setShowConfirmation] = useState(false);

  const { handleSubmit } = useForm();
  const location = useLocation();
  const [finalData, setFinalData] = useState([]);
  const { remainingAptPresData } = useSelector(
    (state) => state?.doctorSchedule
  );
  const user = JSON.parse(localStorage.getItem("family_doc_app"));

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { adminOnlinePrescripionForm, isSuccess, isLoading, isError } =
    useSelector((state) => state?.onlinePrescriptionForm || []);

  function handleCheckboxChange(questData, formAnswereId, labelValue) {
    let finalQuestion;
    let questionIndex = finalData.findIndex(
      (item) => item.questionId === questData.questionId
    );
    if (questionIndex === -1) {
      finalQuestion = {
        fee: adminOnlinePrescripionForm?.data?.formFee,
        patientQuestionId: 0,
        questionId: questData.questionId,
        patientId: user?.userId,
        formId: questData?.formId,
        patientFormAttempt: location?.state + 1,
        isOneTimePayment: remainingAptPresData?.data?.prescription
          ? false
          : true,
        formAnswersList: [
          {
            formAnswereId: formAnswereId,
            textAnswerValue:
              questData?.answerTypeName === "TextBox" ? labelValue : "",
          },
        ],
      };
      const finalizedData = [...finalData, finalQuestion];
      setFinalData(finalizedData);
      return;
    } else {
      let answerIdIndex = finalData[questionIndex].formAnswersList.findIndex(
        (answerObj) => answerObj.formAnswereId === formAnswereId
      );
      if (answerIdIndex !== -1) {
        // Update existing answer
        labelValue == false
          ? (finalData[questionIndex].formAnswersList[answerIdIndex] = null)
          : (finalData[questionIndex].formAnswersList[answerIdIndex] = {
              formAnswereId: formAnswereId,
              textAnswerValue:
                questData?.answerTypeName === "TextBox" ? labelValue : "",
            });
      } else {
        // Add new answer
        if (questData?.answerTypeName !== "Radio") {
          finalData[questionIndex].formAnswersList.push({
            formAnswereId: labelValue ? formAnswereId : null,
            textAnswerValue:
              questData?.answerTypeName === "TextBox" ? labelValue : "",
          });
        } else {
          finalData[questionIndex].formAnswersList[0] = {
            formAnswereId: labelValue ? formAnswereId : null,
            textAnswerValue:
              questData?.answerTypeName === "TextBox" ? labelValue : "",
          };
        }
      }
    }
    setFinalData(
      finalData.map((item) => ({
        ...item,
        formAnswersList: item.formAnswersList.filter(
          (newItem) => newItem !== null
        ),
      }))
    );
  }

  useEffect(() => {
    const finalData = {
      formId: diseaseId,
    };
    dispatch(getAdminPresForm(finalData));
  }, [dispatch, diseaseId]);

  const handlePrescForm = () => {
    setShowSubscription(true);
    setShowConfirmation(false);
  };

  function onCreateSuccess() {
    navigate(PATH.PATIENT_PRESCRIPTION_FORMS);

    const finalData = {
      Search: "",
    };

    dispatch(getAllAdminPresForm(finalData));
  }
  function SubmitOnlinePresForm() {
    setShowConfirmation(true);

    const formSubmit = {
      formQuestionList: finalData?.map((item) => ({
        ...item,
        formAnswersList: item.formAnswersList.filter(
          (newItem) => newItem !== null
        ),
      })),
    };
    if (remainingAptPresData?.data?.prescription && !showConfirmation) {
      dispatch(createPatientPresForm({ formSubmit, onCreateSuccess }));
    } else if (!remainingAptPresData?.data?.prescription && showConfirmation) {
      setFormData(formSubmit);
      handlePrescForm();
    }
  }

  return (
    <>
      <nav aria-label="breadcrumb">
        <ol className="breadcrumb">
          <li className="breadcrumb-item">
            <Link
              to={"/patient/online-prescription-questionnaire"}
              className="text-decoration-none fs-5 color-99"
            >
              Prescription Forms
            </Link>
          </li>
          <IoIosArrowForward className="mx-1 mt-2 color-99" />
          <li
            className="breadcrumb-item active fs-5"
            aria-current="page"
            style={{ color: "#000071" }}
          >
            {adminOnlinePrescripionForm?.data?.title}
          </li>
        </ol>
      </nav>
      <Card className="shadow-sm mt-2">
        {isLoading ? (
          <FullScreenLoader />
        ) : isSuccess ? (
          <Form onSubmit={handleSubmit(SubmitOnlinePresForm)}>
            <Row className="m-3 d-flex justify-content-center flex-column">
              <Col
                xl={7}
                lg={10}
                xs={12}
                className="p-3 rounded mx-auto mb-3"
                style={{
                  boxShadow: "0px 0px 24px 0px #0000000A",
                  borderTop: "6px solid #000071",
                }}
              >
                <h4 className="fw-bold">Prescription Title</h4>
                <p className="m-0" style={{ fontSize: "1.2rem" }}>
                  {adminOnlinePrescripionForm?.data?.title}
                </p>
              </Col>
              {adminOnlinePrescripionForm?.data?.formQuestionList?.length >
              0 ? (
                adminOnlinePrescripionForm?.data?.formQuestionList?.map(
                  (section, sectionIndex) => (
                    <Col
                      key={sectionIndex}
                      xl={7}
                      lg={10}
                      xs={12}
                      className="p-3 rounded mx-auto mb-3"
                      style={{
                        boxShadow: "0px 0px 24px 0px #0000000A",
                        borderLeft: "6px solid #F26522",
                      }}
                    >
                      <Form.Group
                        className="mb-3"
                        controlId={`formQuestion${sectionIndex}`}
                      >
                        <Form.Label className="label-primary mt-2">
                          Q.{`${sectionIndex + 1}.`} {section.question}
                          {section?.isRequired ? (
                            <span
                              style={{ color: "#FF3A3A" }}
                              className="fw-bold"
                            >
                              {" "}
                              *
                            </span>
                          ) : null}
                        </Form.Label>
                        {section?.answerTypeId === 401 ? (
                          <>
                            {section.formAnswersList.map(
                              (option, optionIndex) => (
                                <div key={`radio-${optionIndex}`}>
                                  <Form.Check
                                    inline
                                    required={option?.isRequired}
                                    label={option.answerLabel}
                                    name={`group${sectionIndex}`}
                                    type="radio"
                                    id={`radio-${sectionIndex}-${optionIndex}`}
                                    aria-label={`radio-${sectionIndex}-${optionIndex}`}
                                    onChange={(e) => {
                                      handleCheckboxChange(
                                        section,
                                        option.formAnswereId,
                                        e.target.checked
                                      );
                                    }}
                                  />
                                </div>
                              )
                            )}
                          </>
                        ) : section?.answerTypeId === 404 ? (
                          <>
                            <Form.Control
                              as="textarea"
                              rows={3}
                              placeholder="Answer Text"
                              className="mt-2"
                              required={section?.isRequired}
                              maxLength={250}
                              onChange={(e) => {
                                handleCheckboxChange(
                                  section,
                                  section.formAnswersList[0].formAnswereId,
                                  e.target.value
                                );
                              }}
                            />
                          </>
                        ) : section?.answerTypeId === 402 ? (
                          <>
                            {section.formAnswersList.map(
                              (option, optionIndex) => (
                                <div key={`checkbox-${optionIndex}`}>
                                  <Form.Check
                                    inline
                                    label={option.answerLabel}
                                    name={`group${sectionIndex}`}
                                    // required={option?.isRequired}
                                    type="checkbox"
                                    id={`checkbox-${sectionIndex}-${optionIndex}`}
                                    aria-label={`checkbox-${sectionIndex}-${optionIndex}`}
                                    onChange={(e) => {
                                      handleCheckboxChange(
                                        section,
                                        option.formAnswereId,
                                        e.target.checked
                                      );
                                    }}
                                  />
                                </div>
                              )
                            )}
                          </>
                        ) : null}
                      </Form.Group>
                    </Col>
                  )
                )
              ) : (
                <FullScreenLoader />
              )}
              <Col>
                <Button
                  className={`Admin-Add-btn fw-bold float-end ${
                    isLoading && "disabled"
                  }`}
                  type="submit"
                >
                  {isLoading ? (
                    <ClipLoader
                      color="white"
                      size={25}
                      className="d-flex m-auto"
                    />
                  ) : (
                    "Proceed to Pay"
                  )}
                </Button>
              </Col>
            </Row>
          </Form>
        ) : isError ? (
          <span className="text-danger fst-italic">Network Error</span>
        ) : null}
      </Card>
      <SubscriptionPlanModal
        formData={formData}
        handleClose={() => setShowSubscription(false)}
        show={showSubscription}
      />

      <Modal size="lg" show={showConfirmation}>
        <Modal.Body className="p-4">
          <p className="fw-bold m-0">
            Do you want to check the form and review before saving?
          </p>
        </Modal.Body>
        <Modal.Footer className="d-flex justify-content-center">
          <Button
            onClick={SubmitOnlinePresForm}
            className="Admin-Modal-CancelBtn fw-bold"
          >
            No
          </Button>
          <Button
            onClick={() => setShowConfirmation(false)}
            className="Admin-Add-btn fw-bold"
          >
            Yes
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}
export default DiseaseForm;
