import { Button } from "react-bootstrap";
import FormModal from "../../../../components/Modal/FormModal";
import IMAGES from "../../../../assets/images";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { PATH } from "../../../../utils/path";
import PaymentCheckout from "../PaymentCheckout/PaymentCheckout";
import { useSelector } from "react-redux";

export default function SubscriptionPlanModal({ ...rest }) {
  const { show, handleClose, formData } = rest;

  const [showPayment, setShowPayment] = useState(false);
  const { adminOnlinePrescripionForm } = useSelector(
    (state) => state?.onlinePrescriptionForm || []
  );

  const navigate = useNavigate();

  return (
    <div className="AddSlot_modal">
      <FormModal className="modal-lg" handleClose={handleClose} show={show}>
        <div className="d-flex justify-content-center flex-column align-items-center text-center p-3">
          <img src={IMAGES.BUY_PLAN} alt="purchase plan" />
          <p className="py-3">
            We&apos;re excited to support your health journey! Please click &lsquo;Purchase
            Plan&rsquo; to choose and purchase the plan that suits you best.
          </p>
          <div className="d-flex justify-content-around gap-3">
            <Button
              className="block primary_bg"
              variant="primary"
              type="button"
              onClick={() => {
                handleClose();
                setShowPayment(true);
              }}
            >
              One-Time Payment
            </Button>
            <Button
              className="block primary_bg"
              variant="primary"
              type="button"
              onClick={() => navigate(PATH.PATIENT_PURCHASE_PLANS)}
            >
              Purchase Plan
            </Button>
          </div>
        </div>
      </FormModal>
      {showPayment && (
        <PaymentCheckout
          formData={formData}
          handleClose={() => setShowPayment(false)}
          show={showPayment}
          patientForm={adminOnlinePrescripionForm?.data}
        />
      )}
    </div>
  );
}
